import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController, Platform } from '@ionic/angular';
import { EDAnexosImagenesPermisoTrabajo } from 'src/app/interfaces/edanexos-imagenes-permiso-trabajo';
import { EDFirmaResponsablePermisoTrabajo } from 'src/app/interfaces/edfirma-responsable-permiso-trabajo';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { MaestroCuestionario } from 'src/app/interfaces/maestro-cuestionario';
import { MaestroHijo } from 'src/app/interfaces/maestro-hijo';
import { MaestroPadre } from 'src/app/interfaces/maestro-padre';
import { TrabajadorMigrado } from 'src/app/interfaces/trabajador-migrado';
import { DataQuestionsService } from 'src/app/services/SST/data-questions.service';
import { SaveDataService } from 'src/app/services/SST/save-data.service';
import { UtilsService } from 'src/app/services/utils.service';
import { ModalTrabajadoresComponent } from 'src/app/components/modal-trabajadores/modal-trabajadores.component';

@Component({
  selector: 'app-preview',
  templateUrl: './preview.page.html',
  styleUrls: ['./preview.page.scss'],
})
export class PreviewPage implements OnInit {
  data: EDGuardarPermisoTrabajoAPP;
  /*  */
  listaTrabajadoresMigrados: TrabajadorMigrado[] = [];
  /*  */
  strUbicacionTrabajo: string = '';
  /*  */
  strgAlturaActividad: string = '';
  strFechaYHoraInicioAct: string = '';
  strFechaYHoraFinAct: string = '';
  cuestionario: MaestroCuestionario[] = [];
  listaAnexoImagenes: EDAnexosImagenesPermisoTrabajo[] = [];
  maestrosOrganizados: Record<string, MaestroHijo[]> = {};
  maestrosPadres: MaestroPadre[] = [];

  tituloMaestro: string[] = [];

  strTipoTrabajo: string = '';
  strAntesActividad: string = '';
  strDespuesActividad: string = '';
  strDuranteActividad: string = '';
  strObservaciones: string = '';

  responsables: Record<string, EDFirmaResponsablePermisoTrabajo> = {};
  responsables1: EDFirmaResponsablePermisoTrabajo;
  responsables2: EDFirmaResponsablePermisoTrabajo;
  responsables3: EDFirmaResponsablePermisoTrabajo;

  keysArray: any;

  autorizado: string = '';

  constructor(
    private saveDataService: SaveDataService,
    private utils: UtilsService,
    private questionService: DataQuestionsService,
    private router: Router,
    public modalController: ModalController
  ) {}

  ngOnInit() {
    this.getData();
  }

  verTrabajadores() {
    this.modalController
      .create({
        component: ModalTrabajadoresComponent,
      })
      .then(modal => {
        modal.present();
      });
  }

  private async getData() {
    try {
      console.log('getData');
      this.maestrosPadres = await this.questionService.getAllMaestroPadres();
      await this.utils.presentLoading();
      const dataUrl = history.state.data;
      if (dataUrl) {
        console.log('dataUrl');
        this.saveDataService.setData(dataUrl);
        this.data = this.saveDataService.getData();
      } else {
        console.log('dataUrl else');
        this.data = this.saveDataService.getData();
        this.saveDataService.setData(this.data);
      }
      console.log(this.data);
      this.listaAnexoImagenes = this.data['listaAnexoImagenes'];
      this.listaTrabajadoresMigrados = this.data['listaTrabajadoresMigrados'];
      this.strUbicacionTrabajo = this.data['strUbicacionTrabajo'];
      this.strgAlturaActividad = `${this.data['dblAlturaActividad'].toString()} metros`;
      this.strAntesActividad = this.data['strAntesActividad'];
      this.strDespuesActividad = this.data['strDespuesActividad'];
      this.strDuranteActividad = this.data['strDuranteActividad'];
      this.data['strFechaYHoraInicioAct'] = this.data['strFechaRegistro'];
      this.strFechaYHoraInicioAct = this.data['strFechaYHoraInicioAct'];
      this.strFechaYHoraFinAct = this.data['strFechaYHoraFinAct'];
      let listaCompleta = [this.data['listaItemSeleccionados']] as [][];
      this.cuestionario = this.data['listaMaestroCuestionario'];
      this.strObservaciones = this.data['strObservaciones'];
      this.strTipoTrabajo = this.data['strTipoTrabajo'];
      for (let i = 0; i < listaCompleta.length; i++) {
        for (let j = 0; j < listaCompleta[i].length; j++) {
          let hijos = listaCompleta[i][j] as MaestroHijo[];
          let padre = this.maestrosPadres.find(
            padre => padre.Pk_ID_PermisoAlturas_ItemProteccionPadre == hijos[0].Fk_ID_PermisoAlturas_ItemProteccionPadre
          );
          if (this.maestrosOrganizados[padre.strDescricionPermiso]) {
            this.maestrosOrganizados[padre.strDescricionPermiso].push(hijos[j]);
          } else {
            this.maestrosOrganizados[padre.strDescricionPermiso] = hijos;
          }
        }
      }
      // console.log(this.data['edAutorizadoPermisoTrabajo']);
      // this.responsables = {
      //   'Responsable de la Actividad': this.data['edAutorizadoPermisoTrabajo'],
      //   'Coordinador Alturas': this.data['edCoordinadorTrabajoAlturas'],
      //   'Responsable Plan Emergecia': this.data['edResponsableActivarPlanEmergencia'],
      // };
      // this.keysArray = Object.keys(this.responsables);

      // for (let i = 0; i < this.keysArray.length; i++) {
      //   console.log(JSON.stringify(this.responsables[this.keysArray[i]]));
      // }

      this.responsables1 = this.data['edAutorizadoPermisoTrabajo'];
      this.responsables2 = this.data['edCoordinadorTrabajoAlturas'];
      this.responsables3 = this.data['edResponsableActivarPlanEmergencia'];

      console.log(JSON.stringify(this.responsables1));

      this.autorizado = this.data['Autorizado'] === 'Autorizado ' ? 'SI' : 'NO';

      this.utils.dismissLoading();
    } finally {
      this.utils.dismissLoading();
    }
  }

  getTitulosPadres(): string[] {
    return Object.keys(this.maestrosOrganizados);
  }

  back() {
    this.router.navigate(
      [
        '/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos/seccion-plataforma/descripcion-actividad/proteccion-colectiva/evidencia/aceptar-terminos',
      ],
      { state: { data: this.data } }
    );
  }

  recortarCargo(cargo: string): string {
    const maxLength = 20;
    if (cargo.length > maxLength) {
      return `${cargo.slice(0, maxLength)}...`;
    }
    return cargo;
  }
}
